<style>
  .tab-wraper>.el-tabs>.el-tabs__header {
    margin: 0;
  }
</style>

<template>
  <div>
    <!-- 搜索框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" size="mini" icon="el-icon-circle-check" @click="showBatchUpdatePrice">批量调价</el-button>
        <el-button type="primary" size="mini" icon="el-icon-edit" @click="showBatchUpdateStock">批量调库存</el-button>
        <el-button type="primary" @click="copyStoreGoods" size="mini" icon="el-icon-document">复制门店商品</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <region-city-store @StoreGroupChange="StoreGroupChange"></region-city-store>
          <el-form-item label="SKU编码/商品名称">
            <goods-select ref="GoodsSelect" @DoSelectGoodsHandel="DoSelectGoodsHandel"></goods-select>
          </el-form-item>
          
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    
    <!-- 搜索结果 -->
    <!-- 表格 -->
    <div class="table-wraper tab-wraper" v-loading = "loading">
      <el-tabs v-model="activeName"  @tab-click="handleTabClick">
        <el-tab-pane name="all">
          <span slot="label">全部 {{this.topCardCount.all}}</span>
        </el-tab-pane>
        <el-tab-pane name="onShelf">
          <span slot="label">上架中 {{this.topCardCount.putaWay}}</span>
        </el-tab-pane>
        <el-tab-pane name="downShelf">
          <span slot="label">已下架 {{this.topCardCount.soldOut}}</span>
        </el-tab-pane>
        <my-table ref="myTable" @updateShelfStatus = "updateShelfStatus" @deleteItem="deleteItem" @batchUpdateStock="batchUpdateStock" @doBatchDelHandel="doBatchDelHandel" @updateStockNumHandel="updateStockNumHandel" @batchUpdateShelfStatus="batchUpdateShelfStatus" @batchUpdateSort="batchUpdateSort" @updateSortNumHandel="updateSortNumHandel" @batchUpdatePrice="batchUpdatePrice" @updatePriceNumHandel="updatePriceNumHandel"></my-table>
      </el-tabs>
    </div>
    <!-- 分页条 -->
    <div class="pagination" v-show="tableData.length > 0">
      <el-pagination
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <!-- 批量调价 -->
    <el-dialog title="批量设置价格" :visible.sync="priceDialogFormVisible" width='400px'>
      <el-form>
        <el-form ref="form" label-width="80px">
          <el-form-item label="门店售价" required>
            <el-input v-model="price" size="small" class="search-input" placeholder="请输入门店售价"></el-input>
            <div>所选商品全部将以此价格售卖</div>
          </el-form-item>
        </el-form>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="priceDialogFormVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="doConfirmUpdatePriceHandel" size="mini" :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>
    
    <!-- 批量设置库存 -->
    <el-dialog title="批量设置库存" :visible.sync="dialogFormVisible" width='400px'>
      <el-form>
        <el-form ref="form" label-width="80px">
          <el-form-item label="库存" required>
            <el-input v-model="stockNum" size="small" class="search-input" placeholder="请输入库存"></el-input>
            <div>所选商品全部将以此库存售卖</div>
          </el-form-item>
        </el-form>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 批量设置排序 -->
    <el-dialog title="批量设置排序" :visible.sync="sortDialogFormVisible" width='400px'>
      <el-form>
        <el-form ref="form" label-width="80px">
          <el-form-item label="排序值" required>
            <el-input v-model="sortNum" size="small" class="search-input" placeholder="请输入排序值"></el-input>
            <div>0-999的整数，越大越靠前</div>
          </el-form-item>
        </el-form>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sortDialogFormVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="doConfirmUpdateSortHandel" size="mini" :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>
    
    <!-- 复制门店商品 -->
    <copy-goods ref="copyGoods" @doSearchHandel="doSearchHandel"></copy-goods>
    <!-- 批量调价 -->
    <batch-update-price ref="batchUpdatePrice" @doSearchHandel="doSearchHandel"></batch-update-price>
    <!-- 批量调库存 -->
    <batch-update-stock ref="batchUpdateStock" @doSearchHandel="doSearchHandel"></batch-update-stock>
    
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import MyTable from "./MyTable"
  import CopyStoreGoods from "../../common/RegionCopyStGoods"
  import BatchUpdatePrice from "../../common/BatchUpdatePriceForStore"
  import BatchUpdateStock from "../../common/BatchUpdateStock"
  import GoodsSelect from "@/components/Common/GoodsCommon/SearchByRegion"
  import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore'
  import config from '@/utils/config'
  const storePresaleGoods = {
    // 获取三方平台店内分类
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 预售商品列表
    searchTableData: '/plateform/a/product/store/presale/search',
    // 上下架操作
    batchblxOffShelf: '/plateform/a/product/store/presale/batchblxOffShelf',
    // 批量删除
    batchDel: '/plateform/a/product/store/presale/batchDel',
    // 批量更新库存
    batchUpdateStock: '/plateform/a/product/store/presale/batchUpdateStock',
    // 区域商品选择-商品列表
    selectCityGoods: '/plateform/a/platformCityGoods/presale/selectCityGoods',
    // 批量修改商品权重
    batchUpdateWeight: '/plateform/a/product/store/presale/batchUpdateWeight',
    // 更新门店及时达商品价格
    batchUpdateStorePrice: '/plateform/a/product/store/presale/batchUpdateStorePrice'
  }
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        activeName: 'all',
        searchContent: {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          cityId: '', // 城市id
          storeGroupTypeNo: '',
          storeGroupNo: '',
          queryString: '', // 查询条件
          platformId: 5, // 平台类型：1-有赞；2-京东；3-美团；4-饿百；5-商城
          saleType: 4, // 售卖类型：1：常规；2：预售；3:长周期预售，4：及时达(查询对应售卖类型数据该字段必传，不传入择默认全部)
          platformCityGoodsIds: [], // 平台区域商品ID
          storeNo: '', // 门店编号
          categoryId: '',
          categoryIdList: [],
          status: -1 // 上下架状态：-1-全部；0-上架；1-下架
        },
        // 默认分类参数
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        categoryList: [], // 商品分类id
        tableData: [],
        // 选中的门店预售商品
        checkedList: [],
        isDisabled: false, // 控制设置库存防止多次点击
        // 控制批量设置库存弹框
        dialogFormVisible: false,
        // 库存数量 批量更新库存时必传
        stockNum: '',
        // 选项卡顶部内容
        topCardCount: {
          all: 0,
          putaWay: 0, // 上架
          soldOut: 0 // 下架
        },

        // 控制批量调价弹框
        priceDialogFormVisible: false,
        price: '', // 批量调价必传

        // 批量设置排序
        sortDialogFormVisible: false,
        // 排序值
        sortNum: ''

      }
    },
    created() {
      let platformCityGoodsId = this.$route.query.platformCityGoodsId
      if(platformCityGoodsId) {
        this.searchContent.platformCityGoodsIds.push(platformCityGoodsId)
        this.searchContent.queryString = platformCityGoodsId
      }
      // 获取三方平台店内分类
      this.getCategoryId()
    },
    components: {
      "my-table": MyTable,
      "copy-goods": CopyStoreGoods,
      "batch-update-price": BatchUpdatePrice,
      "batch-update-stock": BatchUpdateStock,
      "goods-select": GoodsSelect,
      "region-city-store": RegionCityStore
    },
    mounted() {
      // 根据用户选择的城市获取数据
      this.doSearchHandel(true)

      // 商品筛选组件初始化
      this.goodsSelectInit()
    },
    methods: {
      StoreGroupChange(params) {
        this.searchContent.regionCode = params.regionCode
        this.searchContent.cityId = params.cityId
        this.searchContent.storeNo = params.storeNo
        this.searchContent.storeGroupTypeNo = params.storeGroupTypeNoList[0]
        this.searchContent.storeGroupNo = params.storeGroupTypeNoList[1]
        this.doSearchHandel(true)
      },
      /**
       * [getCategoryId 三方平台店内分类]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(storePresaleGoods.getCategoryTree).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(三方平台店内分类):", e.message)
        })
      },
      /**
       * [handleTabClick tab点击切换]
       * @return {[type]} [description]
       */
      handleTabClick(_val) {
        if(_val.name == 'all') {
          this.searchContent.status = -1
        }else if(_val.name == 'onShelf') {
          this.searchContent.status = 0
        }else if(_val.name == 'downShelf') {
          this.searchContent.status = 1
        }
        this.activeName = _val.name
        this.doSearchHandel(true)

      },
      /**
       * [changeSize 分页]
       * @param  {[type]} _val [description]
       * @return {[type]}      [description]
       */
      changeSize(value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      changeCurrent(value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 搜索]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }

        this.$http.post(storePresaleGoods.searchTableData, {...this.searchContent, pageSize: this.pageSize, pageNum: this.pageNum}).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.pageVo && res.data.pageVo.result && res.data.pageVo.result.length) {
              for(let item of res.data.pageVo.result) {
                item.showUpdate = false
                item.updateStockNum = item.stockNum

                item.showSortNumUpdate = false
                item.updateSortNum = item.sortNum

                item.showPriceUpdate = false
                item.updatePrice = item.price

                item.uniqueId = `${item.presaleId}${item.productNo}`
              }
              this.tableData = res.data.pageVo.result
              let _value = {
                tableData: this.tableData
              }

              if(this.$refs.myTable) {
                this.$refs.myTable.setDefaultMesg(_value)
              }

              if(this.pageNum === 1) {
                this.total = res.data.pageVo.total
              }
            }else {
              this.tableData = []
              let _value = {
                tableData: this.tableData
              }
              if(this.$refs.myTable) {
                this.$refs.myTable.setDefaultMesg(_value)
              }

              if(this.pageNum === 1) {
                this.total = 0
              }
            }

            if(res.data && res.data.countVo) {
              this.topCardCount.all = res.data.countVo.all
              this.topCardCount.putaWay = res.data.countVo.putaWay
              this.topCardCount.soldOut = res.data.countVo.soldOut
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [updateShelfStatus 更新上下架]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      updateShelfStatus(_value) {
        // 预售商品ID集合
        let presaleIds = []
        presaleIds.push(_value.row.presaleId)

        // status 上下架状态：0-上架；1-下架
        let shelfStatus = -1
        let shelfStatusName = ''
        if(_value.shelfStatus == 0) {
          shelfStatus = 0
          shelfStatusName = '上架'
        }else if(_value.shelfStatus == 1) {
          shelfStatus = 1
          shelfStatusName = '下架'
        }

        this.$confirm('确定要执行(' + `${shelfStatusName}` + ')操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(storePresaleGoods.batchblxOffShelf, {'presaleIds': presaleIds, 'shelfStatus': shelfStatus}).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              if(shelfStatus === 0) {
                this.$message.success("上架成功")
              }else if(shelfStatus === 1) {
                this.$message.success("下架成功")
              }
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [deleteItem 删除]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      deleteItem(_value) {
        // 预售商品ID集合
        let presaleIds = []
        presaleIds.push(_value.row.presaleId)
        this.$confirm('确定要执行删除操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(storePresaleGoods.batchDel, {'presaleIds': presaleIds, 'deleteStatus': 1}).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel(true)
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [doBatchDelHandel 批量删除]
       * @return {[type]} [description]
       */
      doBatchDelHandel(_value) {
        if(_value.checkedList) {
          this.checkedList = _value.checkedList
        } 
        if(this.checkedList.length === 0) {
          this.$message("请选择商品")
          return 
        }
        // 预售商品ID集合
        let presaleIds = []
        for(let item of this.checkedList) {
          presaleIds.push(item.presaleId)
        }
        this.$confirm('确定要执行批量删除操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(storePresaleGoods.batchDel, {'presaleIds': presaleIds, 'deleteStatus': 1}).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel(true)
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [batchUpdatePrice 批量设置门店售价]
       * @return {[type]} [description]
       */
      batchUpdatePrice(_value) {
        if(_value.checkedList) {
          this.checkedList = _value.checkedList
        } 
        this.priceDialogFormVisible = true
        if(_value.checkedList.length === 1) {
          this.price = _value.checkedList[0].price
        }else {
          this.price = ''
        }
      },
      /**
       * [batchUpdateStock 批量设置库存]
       * @return {[type]} [description]
       */
      batchUpdateStock(_value) {
        if(_value.checkedList) {
          this.checkedList = _value.checkedList
        } 
        this.dialogFormVisible = true
        if(_value.checkedList.length === 1) {
          this.stockNum = _value.checkedList[0].stockNum
        }else {
          this.stockNum = ''
        }
      },
      /**
       * [doConfirmHandel 点击确定按钮确定更新库存]
       * @return {[type]} [description]
       */
      doConfirmHandel() {
        let numReg = /^[1-9]*[1-9][0-9]*$/
        if(this.checkedList.length === 0) {
          this.$message("请选择商品")
          return 
        }

        if(!this.stockNum) {
          this.$message("请输入库存数量")
          return 
        }else if(this.stockNum == 0) {
          this.stockNum = 0
        }else {
          if(numReg.test(this.stockNum)) {
            if(this.stockNum < 0 || this.stockNum > 999) {
              this.$message("库存数为0-999的整数")
              return
            }
          }else {
            this.$message("库存数为0-999的整数")
            return
          }
        } 

        let presaleIds = []
        for(let item of this.checkedList) {
          presaleIds.push(item.presaleId)
        }
        let stockNum = parseInt(this.stockNum)
        this.isDisabled = true
        this.$http.post(storePresaleGoods.batchUpdateStock, {'presaleIds': presaleIds, 'stockNum': stockNum}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.dialogFormVisible = false
            this.$message.success("批量设置库存成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [updateStockNumHandel 更新库存]
       * @return {[type]} [description]
       */
      updateStockNumHandel(_value) {
        if(!_value) {
          return
        }
        // 预售商品ID集合
        let presaleIds = []
        presaleIds.push(_value.row.presaleId)

        let stockNum = parseInt(_value.stockNum)
        utils.showLoading()
        this.$http.post(storePresaleGoods.batchUpdateStock, {'presaleIds': presaleIds, 'stockNum': stockNum}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            this.dialogFormVisible = false
            this.$message.success("设置库存成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [copyStoreGoods 复制门店商品]
       * @return {[type]} [description]
       */
      copyStoreGoods() {
        let _value = {
          title: '复制门店商品',
          saleType: this.searchContent.saleType,
          platformId: this.searchContent.platformId,
          regionCode: this.searchContent.regionCode
        }
        this.$refs.copyGoods.setDefaultMesg(_value)
      },
      /**
       * [showBatchUpdatePrice 批量调价]
       * @return {[type]} [description]
       */
      showBatchUpdatePrice() {
        let _value = {
          title: '批量调价',
          storeNo: this.searchContent.storeNo,
          saleType: this.searchContent.saleType,
          platformId: this.searchContent.platformId,
          regionCode: this.searchContent.regionCode
        }
        this.$refs.batchUpdatePrice.setDefaultMesg(_value)
      },
      /**
       * [showBatchUpdateStock 批量设置库存]
       * @return {[type]} [description]
       */
      showBatchUpdateStock() {
        let _value = {
          title: '批量设置库存',
          storeNo: this.searchContent.storeNo,
          saleType: this.searchContent.saleType,
          platformId: this.searchContent.platformId,
          regionCode: this.searchContent.regionCode
        }
        this.$refs.batchUpdateStock.setDefaultMesg(_value)
      },
      /**
       * [batchUpdateShelfStatus 批量上下架]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      batchUpdateShelfStatus(_value) {
        if(_value.checkedList) {
          this.checkedList = _value.checkedList
        } 
        if(this.checkedList.length === 0) {
          this.$message("请选择商品")
          return 
        }
        // 预售商品ID集合
        let presaleIds = []
        for(let item of this.checkedList) {
          presaleIds.push(item.presaleId)
        }

        // status 上下架状态：0-上架；1-下架
        let shelfStatus = -1
        let shelfStatusName = ''
        if(_value.shelfStatus == 0) {
          shelfStatus = 0
          shelfStatusName = '上架'
        }else if(_value.shelfStatus == 1) {
          shelfStatus = 1
          shelfStatusName = '下架'
        }

        this.$confirm('确定要执行批量(' + `${shelfStatusName}` + ')操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(storePresaleGoods.batchblxOffShelf, {'presaleIds': presaleIds, 'shelfStatus': shelfStatus}).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              if(shelfStatus === 0) {
                this.$message.success("批量上架成功")
              }else if(shelfStatus === 1) {
                this.$message.success("批量下架成功")
              }
              this.doSearchHandel()
              return
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [batchUpdateSort 批量更新顺序]
       * @return {[type]} [description]
       */
      batchUpdateSort(_value) {
        if(_value.checkedList) {
          this.checkedList = _value.checkedList
        } 
        this.sortDialogFormVisible = true
        if(_value.checkedList.length === 1) {
          this.sortNum = _value.checkedList[0].sortNum
        }else {
          this.sortNum = ''
        }
      },
      /**
       * [doConfirmUpdateSortHandel 批量更新顺序事件]
       * @return {[type]} [description]
       */
      doConfirmUpdateSortHandel() {
        let numReg = /^[1-9]*[1-9][0-9]*$/
        if(this.checkedList.length === 0) {
          this.$message("请选择商品")
          return 
        }

        if(this.sortNum == "") {
          this.$message("请输入排序值")
          return 
        }else if(this.sortNum == 0) {
          this.sortNum = 0
        }else {
          if(numReg.test(this.sortNum)) {
            if(this.sortNum < 0 || this.sortNum > 999) {
              this.$message("排序值为0-999的整数")
              return
            }
          }else {
            this.$message("排序值为0-999的整数")
            return
          }
        } 

        let presaleIds = []
        for(let item of this.checkedList) {
          presaleIds.push(item.presaleId)
        }
        let sortNum = parseInt(this.sortNum)
        this.isDisabled = true
        this.$http.post(storePresaleGoods.batchUpdateWeight, {'presaleIds': presaleIds, 'sortNum': sortNum}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.sortDialogFormVisible = false
            this.$message.success("批量设置排序成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [updateSortNumHandel 当条更新排序值]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      updateSortNumHandel(_value) {
        if(!_value) {
          return
        }
        // 预售商品ID集合
        let presaleIds = []
        presaleIds.push(_value.row.presaleId)

        let sortNum = parseInt(_value.sortNum)
        utils.showLoading()
        this.$http.post(storePresaleGoods.batchUpdateWeight, {'presaleIds': presaleIds, 'sortNum': sortNum}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            this.sortDialogFormVisible = false
            this.$message.success("设置排序成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doConfirmUpdatePriceHandel 批量调售价]
       * @return {[type]} [description]
       */
      doConfirmUpdatePriceHandel() {
        let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
        if(this.checkedList.length === 0) {
          this.$message("请选择商品")
          return 
        }

        if(!this.price) {
          this.$message("请输入门店售价")
          return 
        }else {
          if(!salePriceReg.test(this.price)) {
            this.$message("门店售价为大于0，小数保留小数点后2位")
            return
          }
        } 

        let storePlatformGoodsIds = []
        for(let item of this.checkedList) {
          storePlatformGoodsIds.push(item.presaleId)
        }
        let price = parseFloat(this.price)
        this.isDisabled = true
        this.$http.post(storePresaleGoods.batchUpdateStorePrice, {'storePlatformGoodsIds': storePlatformGoodsIds, 'price': price}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.priceDialogFormVisible = false
            this.$message.success("批量设置价格成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [updateSortNumHandel 当条更新门店售价]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      updatePriceNumHandel(_value) {
        if(!_value) {
          return
        }
        // 预售商品ID集合
        let storePlatformGoodsIds = []
        storePlatformGoodsIds.push(_value.row.presaleId)

        let price = parseFloat(_value.price)
        utils.showLoading()
        this.$http.post(storePresaleGoods.batchUpdateStorePrice, {'storePlatformGoodsIds': storePlatformGoodsIds, 'price': price}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            this.sortDialogFormVisible = false
            this.$message.success("设置门店售价成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },


      /**
       * [goodsSelectInit 商品筛选组件初始化]
       * @return {[type]} [description]
       */
      goodsSelectInit() {
        if(this.$refs.GoodsSelect) {
          let _val = {
            queryString: this.searchContent.queryString,
            categoryIdList: this.searchContent.categoryIdList,
            saleType: this.searchContent.saleType,
            platformId: this.searchContent.platformId,
            platformCityGoodsIds: this.searchContent.platformCityGoodsIds
          }
          this.$refs.GoodsSelect.setDefaultMesg(_val)
        }
      },
      /**
       * [DoSelectGoodsHandel 确认选择]
       */
      DoSelectGoodsHandel(_params) {
        let checkedList = _params.checkedList
        let queryString = _params.queryString
        let platformCityGoodsIds = []
        for(let item of checkedList) {
          platformCityGoodsIds.push(item.platformCityGoodsId)
        }
        this.searchContent.platformCityGoodsIds = platformCityGoodsIds
        this.searchContent.queryString = queryString
        this.doSearchHandel(true)
      }
    }
  }
</script>


