<template>
  <div>
    <el-dialog 
      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :title="title" 
      :visible.sync="isShow" 
      width='820px'>
      <el-form ref="form" label-width="70px" @submit.native.prevent>
        <el-form-item label="从" required>
          <el-select class="add-input" v-model="addParamMesg.fromStoreNo" filterable placeholder="请选择门店" size="small">
            <el-option
              v-for="item in storeNoOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="复制到" required>
          <region-store :regionCode="searchContent.regionCode" :platformId="searchContent.platformId" getStoreListUrl="/plateform/a/platformStore/getPlatformStoreByCityId" :showGroup="true" @setCheckedData="setCheckedData"></region-store>
        </el-form-item>
        <div>
          <p class='des-p-style'>1、可以将A门店有，B门店没有（ERP上已引入本门店）的商品，复制到B门店；</p>
          <p class='des-p-style'>2、可以将单个门店商品，复制到本城市多个门店中，前提是商品引入了门店；</p>
          <p class='des-p-style'>3、使用场景，新门店上线无商品时，直接复制本城市已有商品的门店，或者门店商品不全，需要复制本城市商品比较全的门店的商品。</p>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import RegionStore from '@/components/Common/StoreCommon/RegionStore'
  const requestUrl = {
    // 获取城市平台门店
    getPlatformStoreByCityId: '/plateform/a/platformStore/getPlatformStoreByCityId',
    // 复制商品
    copyProduct: '/plateform/a/product/store/presale/copyProduct'

  }
  export default {
    data() {
      return {
        isDisabled: false, // 防止多次点击确定按钮
        // 是否显示弹框
        isShow: false,
        title: '复制门店商品',
        addParamMesg: {
          DIFFCITY: true,
          saleType: '',
          fromStoreNo: '', // 商品来源门店编码
          platformId: '',
          toStoreNo: [] // 商品落点门编码
        },
        storeNoOptions: [],
        checkedList: [], // 选中的门店
        searchContent: {
          DIFFCITY: true,
          regionCode: '',
          searchValue: '',
          platformId: '',
          cityId: '',
          storeGroupTypeNo: '',
          storeGroupNo: '',
          storeGroupTypeNoList: []
        }

      }
    },
    components: {
      "region-store": RegionStore
    },
    methods: {
      /**
       * [setDefaultMesg 设置默认]
       */
      setDefaultMesg(params) {
        this.isShow = true
        this.title = params.title || ''
        this.addParamMesg.platformId = params.platformId || ''
        this.addParamMesg.saleType = params.saleType || ''

        // 重置弹窗内容
        this.searchContent = {
          DIFFCITY: true,
          regionCode: params.regionCode,
          searchValue: '',
          platformId: params.platformId,
          cityId: '',
          storeGroupTypeNo: '',
          storeGroupNo: '',
          storeGroupTypeNoList: []
        }
        this.checkedList = []

        this.getStoreList()
      },
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
      getStoreList() {
        this.addParamMesg.fromStoreNo = ''
        this.$http.post(requestUrl.getPlatformStoreByCityId, {...this.searchContent}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              this.addParamMesg.fromStoreNo = res.data[0].storeNo
              this.storeNoOptions = res.data.map(item => {
                return {
                  value: item.storeNo,
                  label: item.storeNo +'-'+ item.storeName
                }
              })
            }else {
              this.storeNoOptions = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("【提示】获取门店信息错误：", e.message)
        })
      },
      /**
       * [confirmClose 点击确定按钮触发事件操作]
       * @return {[type]} [description]
       */
      confirmClose() {
        let toStoreNoArr = this.checkedList.map(item => {
          return item.storeNo
        })
        this.addParamMesg.toStoreNo = toStoreNoArr
        if(this.addParamMesg.toStoreNo.length === 0) {
          this.$message.success("请选择要复制到的门店")
          return
        }
        this.isDisabled = true
        this.$http.post(requestUrl.copyProduct, {...this.addParamMesg}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.isShow = false
            this.$message.success("复制门店商品成功")
            this.$emit("doSearchHandel")
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * @description: 设置选择门店的值
       * @param {type} 
       * @return: 
       */
      setCheckedData(checkedList) {
        this.checkedList = checkedList || []
      }
    }
    
  }
</script>


