<style>
  .p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .img-w {
    width: 80px;
    height: 80px;
  }
  .img-w>img {
    width: 80px;
  }
  .p-m-c-r {
    padding: 0 10px;
  }
  .erp-p-no {
    color: #666;
    font-size: 12px;
  }
  
  .stock-num .el-button--mini{
    padding: 6px 5px;
  }
  .act-name-wrap {
    margin: 4px 0;
    padding: 0 5px;
    border: 1px solid red;
    line-height: 20px;
    text-align: center;
    box-sizing: border-box;
  }

</style>
<template>
  <div>
    <div class="batch-handel-w">
      <el-button class="sup-mini" size="mini" icon="el-icon-circle-check" @click="showSetPriceDialog" :disabled="checkedList.length === 0">设置价格</el-button>
      <el-button class="sup-mini" size="mini" icon="el-icon-edit" @click="showSetStockDialog" :disabled="checkedList.length === 0">设置库存</el-button>
      <el-button class="sup-mini" size="mini" icon="el-icon-upload2" @click="batchUpdateShelfStatus(0)" :disabled="checkedList.length === 0">上架</el-button>
      <el-button class="sup-mini" size="mini" icon="el-icon-download" @click="batchUpdateShelfStatus(1)" :disabled="checkedList.length === 0">下架</el-button>
      <el-button class="sup-mini" size="mini" icon="el-icon-sort" @click="showBatchSort" :disabled="checkedList.length === 0">设置排序</el-button>
      <el-button class="sup-mini" size="mini" icon="el-icon-delete" @click="doBatchDelHandel" :disabled="checkedList.length === 0">删除</el-button>
    </div>
    <el-table
      :data="tableData"
      empty-text="没有查询到您想要的数据"
      style="width:100%;"
      size="small"
      @select="handleSelect"
      @select-all="handleSelectAll">
      <el-table-column
        type="selection"
        align="center">
      </el-table-column>
      <el-table-column label="门店信息" min-width="120px">
        <template slot-scope="scope">
          <div>
            <p>{{scope.row.storeName}}</p>
            <p class="erp-p-no">ERP编码：{{scope.row.storeNo}}</p>
          </div>
        </template>  
      </el-table-column>
      <el-table-column label="商品信息" min-width="256px">
        <template slot-scope="scope">
          <div class="p-m-w">
            <div class="img-w">
              <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
              <img v-else src="/shop/static/img_error.png" alt="">
            </div>
            <div class="p-m-c-r">
              <div>{{scope.row.productName || '--'}}</div>
              <div class="erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
              <p class="erp-p-no">门店商品ID：{{scope.row.presaleId || '--'}}</p>
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="营销活动" min-width="110px">
        <template slot-scope="scope">
          <div v-if="scope.row.activitys && scope.row.activitys.length">
            <div v-for="(item ,idx) in scope.row.activitys" :key="idx" class="act-name-wrap">
              {{item}}
            </div>
          </div>
          <div v-else>--</div>
        </template>
      </el-table-column>
      <el-table-column label="门店售价" min-width="160px">
        <template slot-scope="scope">
          <div v-if="scope.row.showPriceUpdate == false" class="stock-num">
            <el-button-group>
              <el-input v-model="scope.row.price" size="mini" style="width: 80px;" @focus="modifyPriceNum(scope.$index, scope.row)" :disabled="scope.row.activitys && scope.row.activitys.length ? true : false"></el-input>
            </el-button-group>
          </div>
          <div v-else-if="scope.row.showPriceUpdate == true" class="stock-num">
            <el-button-group>
              <el-input v-model="scope.row.updatePrice" size="mini" style="width: 80px;" @blur="deletePriceNum(scope.$index, true)"></el-input>
            </el-button-group>
            <el-button-group>
              <el-button icon="el-icon-check" size="mini" style="width: 25px;" @click="sureUpdataPriceNum(scope.row)"></el-button>
              <el-button icon="el-icon-close" size="mini" style="width: 25px;" @click="deletePriceNum(scope.$index)"></el-button>
            </el-button-group>
          </div>
        </template>
      </el-table-column>

      <el-table-column label="库存" min-width="160px">
        <template slot-scope="scope">
          <div v-if="scope.row.showUpdate == false" class="stock-num">
            <el-button-group>
              <el-input v-model="scope.row.stockNum" size="mini" style="width: 80px;" @focus="modifystockNum(scope.$index, scope.row)"></el-input>
            </el-button-group>
          </div>
          <div v-else-if="scope.row.showUpdate == true" class="stock-num">
            <el-button-group>
              <el-input v-model="scope.row.updateStockNum" size="mini" style="width: 80px;" @blur="deleteStockNum(scope.$index, true)"></el-input>
            </el-button-group>
            <el-button-group>
              <el-button icon="el-icon-check" size="mini" style="width: 25px;" @click="sureUpdataStockNum(scope.row)"></el-button>
              <el-button icon="el-icon-close" size="mini" style="width: 25px;" @click="deleteStockNum(scope.$index)"></el-button>
            </el-button-group>
          </div>
        </template>
      </el-table-column>

      <el-table-column label="排序" min-width="160px">
        <template slot-scope="scope">
          <div v-if="scope.row.showSortNumUpdate == false" class="stock-num">
            <el-button-group>
              <el-input v-model="scope.row.sortNum" size="mini" style="width: 80px;" @focus="modifySortNum(scope.$index, scope.row)"></el-input>
            </el-button-group>
          </div>
          <div v-else-if="scope.row.showSortNumUpdate == true" class="stock-num">
            <el-button-group>
              <el-input v-model="scope.row.updateSortNum" size="mini" style="width: 80px;" @blur="deleteSortNum(scope.$index, true)"></el-input>
            </el-button-group>
            <el-button-group>
              <el-button icon="el-icon-check" size="mini" style="width: 25px;" @click="sureUpdataSortNum(scope.row)"></el-button>
              <el-button icon="el-icon-close" size="mini" style="width: 25px;" @click="deleteSortNum(scope.$index)"></el-button>
            </el-button-group>
          </div>
        </template>
      </el-table-column>

      <el-table-column prop="status" label="状态" :formatter="statusFormatter"></el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="100">
        <template slot-scope="scope" size="small">
          <div>
            <el-button type="text" size="mini" v-if="scope.row.status === 1" @click="updateShelfStatus(scope.row, '0')" icon="el-icon-upload2">上架</el-button>
            <el-button type="text" size="mini" v-else-if="scope.row.status === 0" @click="updateShelfStatus(scope.row, '1')" icon="el-icon-download">下架</el-button>
          </div>
          <div>
            <el-button type="text" size="mini" icon="el-icon-delete" @click="deleteItem(scope.row)">删除</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import * as utils from '@/utils/utils'
export default {
  data() {
    return {
      tableData: [],
      // 选中的商品
      checkedList: []
    }
  },
  methods: {
    /**
     * [setDefaultMesg 设置信息]
     * @param {[type]} _val [description]
     */
    setDefaultMesg(_val) {
      this.tableData = _val.tableData
      this.checkedList = []
    },
    /**
     * [statusFormatter 格式化状态]
     */
    statusFormatter(row, column, cellValue, index) {
      if(cellValue === 0) {
        return '上架'
      }else if(cellValue === 1) {
        return '下架'
      } 
    },
    /**
     * [updateShelfStatus 上下架]
     * @param  {[type]} _row    [当前行信息]
     * @param  {[type]} _status [状态  0  上架  1  下架]
     * @return {[type]}         [description]
     */
    updateShelfStatus(_row, _shelfStatus) {
      let _value = {
        row: _row,
        shelfStatus: _shelfStatus
      }

      this.$emit('updateShelfStatus', _value)
    },
    /**
     * [deleteItem 删除操作]
     * @param  {[type]} _row [description]
     * @return {[type]}      [description]
     */
    deleteItem(_row) {
      let _value = {
        row: _row
      }

      this.$emit('deleteItem', _value)
    },
    /**
     * [handleSelect 选择某一个]
     * @return {[type]} [description]
     */
    handleSelect(val, row) {
      let hash = {}
      let checkedListTemp = []

      if(utils.isKeyInArrayBykey(row.uniqueId, 'uniqueId', this.checkedList)) {
        this.checkedList = utils.removeItemByKeyVal('uniqueId', row.uniqueId, this.checkedList)
        return
      }else {
        checkedListTemp.push(row)
      }

      let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
      this.checkedList = checkedListTempArr.reduceRight((item, next) => {
        hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
        return item
      }, [])
    },
    /**
     * [handleSelectAll 选择多个]
     * @return {[type]} [description]
     */
    handleSelectAll(val) {
      let hash = {}
      if(val.length > 0) {        
        // 全选
        for(let item of val) {
          this.checkedList.push(item)
        }
        // 去重
        this.checkedList = this.checkedList.reduceRight((item, next) => {
          hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
          return item
        }, [])
      }else { 
        // 全不选 此时val.length<=0
        // 1.获取当前table中每一条的id
        let checkedListTemp = []
        for(let item of this.tableData) {
          checkedListTemp.push(item)
        }
        // 2.从要传递的参数中去掉这些重复的
        let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'uniqueId')
        this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
      }
    },
    /**
     * [showSetPriceDialog 设置门店售价]
     * @return {[type]} [description]
     */
    showSetPriceDialog() {
      let state = this.checkedList.some((item, idx) => {
        return item.activitys && item.activitys.length
      })
      if(state) {
        this.$notify.warning({
          title: '温馨提示',
          message: '所选商品中部分商品因参加活动不能进行批量调价，请取消选择再进行批量调价！'
        })
        return
      }
      let _value = {
        checkedList: this.checkedList
      }

      this.$emit('batchUpdatePrice', _value)
    },
    /**
     * [showSetStockDialog 设置库存]
     * @return {[type]} [description]
     */
    showSetStockDialog() {
      let _value = {
        checkedList: this.checkedList
      }

      this.$emit('batchUpdateStock', _value)
    },
    /**
     * [doBatchDelHandel 批量删除]
     * @return {[type]} [description]
     */
    doBatchDelHandel() {
      let _value = {
        checkedList: this.checkedList
      }

      this.$emit('doBatchDelHandel', _value)
    },
    /**
     * [deletePriceNum 删除手动调价]
     * @return {[type]} [description]
     */
    deletePriceNum(index, val) {
      setTimeout(() => {
        this.tableData[index].updatePrice = this.tableData[index].price
        this.tableData[index].showPriceUpdate = false
      }, 500)
    },
    /**
     * [deleteStockNum 删除手动调价]
     * @return {[type]} [description]
     */
    deleteStockNum(index, val) {
      setTimeout(() => {
        this.tableData[index].updateStockNum = this.tableData[index].stockNum
        this.tableData[index].showUpdate = false
      }, 500)
    },
    /**
     * [deleteSortNum 删除输入排序值]
     * @return {[type]} [description]
     */
    deleteSortNum(index, val) {
      setTimeout(() => {
        this.tableData[index].updateSortNum = this.tableData[index].sortNum
        this.tableData[index].showSortNumUpdate = false
      }, 500)
    },
    /**
     * [modifyPriceNum 输入]
     * @return {[type]} [description]
     */
    modifyPriceNum(index, val) {
      if(val.showPriceUpdate == true) {
        return
      }
      this.tableData[index].showPriceUpdate = true
    },
    /**
     * [modifystockNum 输入]
     * @return {[type]} [description]
     */
    modifystockNum(index, val) {
      if(val.showUpdate == true) {
        return
      }
      this.tableData[index].showUpdate = true
    },
    /**
     * [modifySortNum 输入sortNum排序值]
     * @return {[type]} [description]
     */
    modifySortNum(index, val) {
      if(val.showSortNumUpdate == true) {
        return
      }
      this.tableData[index].showSortNumUpdate = true
    },
    /**
     * [sureUpdataPriceNum 确定更新售价]
     * @param  {[type]} _val [description]
     * @return {[type]}      [description]
     */
    sureUpdataPriceNum(_val) {
      let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
      let price = _val.updatePrice

      if(!price) {
        this.$message("请输入门店售价")
        return 
      }else {
        if(!salePriceReg.test(price)) {
          this.$message("门店售价为大于0，小数保留小数点后2位")
          return
        }
      }

      let _value = {
        row: _val,
        price: price
      }
      this.$emit('updatePriceNumHandel', _value)
    },
    /**
     * [sureUpdataStockNum 确定更新库存]
     * @param  {[type]} _val [description]
     * @return {[type]}      [description]
     */
    sureUpdataStockNum(_val) {
      let numReg = /^[1-9]*[1-9][0-9]*$/
      let stockNum = _val.updateStockNum
      if(stockNum == "") {
        this.$message("请输入库存数量")
        return 
      }else if(stockNum == 0) {
        stockNum = 0
      }else {
        if(numReg.test(stockNum)) {
          if(stockNum < 0 || stockNum > 999) {
            this.$message("库存数为0-999的整数")
            return
          }
        }else {
          this.$message("库存数为0-999的整数")
          return
        }
      } 

      let _value = {
        row: _val,
        stockNum: stockNum
      }
      this.$emit('updateStockNumHandel', _value)
    },
    /**
     * [sureUpdataSortNum 确定更新排序值]
     * @param  {[type]} _val [description]
     * @return {[type]}      [description]
     */
    sureUpdataSortNum(_val) {
      let numReg = /^[1-9]*[1-9][0-9]*$/
      let sortNum = _val.updateSortNum
      if(sortNum == "") {
        this.$message("请输入库存数量")
        return 
      }else if(sortNum == 0) {
        sortNum = 0
      }else {
        if(numReg.test(sortNum)) {
          if(sortNum < 0 || sortNum > 999) {
            this.$message("库存数为0-999的整数")
            return
          }
        }else {
          this.$message("库存数为0-999的整数")
          return
        }
      } 

      let _value = {
        row: _val,
        sortNum: sortNum
      }
      this.$emit('updateSortNumHandel', _value)
    },
    /**
     * [batchUpdateShelfStatus 批量上下架]
     * @param  {[type]} _shelfStatus [description]
     * @return {[type]}              [description]
     */
    batchUpdateShelfStatus(_shelfStatus) {
      let _value = {
        shelfStatus: _shelfStatus,
        checkedList: this.checkedList
      }
      this.$emit('batchUpdateShelfStatus', _value)
    },
    /**
     * [showBatchSort 批量设置排序]
     * @return {[type]} [description]
     */
    showBatchSort() {
      let _value = {
        checkedList: this.checkedList
      }

      this.$emit('batchUpdateSort', _value)
    }

  }
}
</script>



