<style>
.p-t {
  margin-bottom: 10px;
}

.u-btn-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.file-name-wrap {
  width: 200px;
  height: 32px;
  line-height: 32px;
  overflow: hidden;
  border: 1px solid #ececec;
  border-radius: 4px;
  padding: 0 10px;
  position: relative;
  top: 5px;
}

.label-wrap {
  border: 1px solid #ececec;
  border-radius: 4px;
  position: relative;
  top: 5px;
  height: 32px;
  line-height: 32px;
}

.label-wrap > .excel-file-label {
  line-height: 32px;
  cursor: pointer;
  padding: 7px 10px;
}

.label-wrap > .excel-file-label:hover {
  color: #fff;
  background-color: #66b1ff;
  border-radius: 4px;
}
</style>

<template>
  <div>
    <el-dialog 
      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :title="title" 
      :visible.sync="isShow" 
      width='450px'>
      <el-form ref="form" label-width="80px">
        <el-form-item label="选择门店">
          <el-select class="search-date-picker" v-model="storeNo" filterable placeholder="请选择门店" clearable size="small">
            <el-option
              v-for="item in storeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <span>下载模板必选</span>
        </el-form-item>
        <el-form-item label="本地上传">
          <div class="excel-u-d-wrap">
            <div class="u-btn-wrap">
              <div class="file-name-wrap">{{ file.name }}</div>
              <div class="label-wrap">
                <label for="excelFile" class="excel-file-label">
                  <input type="file" id="excelFile" hidden @change="handleFileSelect"/>
                  <span class="u-txt">选择文件</span>
                </label>
              </div>
            </div>
            <div class="d-btn-wrap">
              <span>请按EXCEL模板格式导入</span>
              <el-button type="text" @click="downloadModel" size="mini" :disabled="isDisabled">下载模板</el-button>
            </div>
          </div>
        </el-form-item>
      </el-form>


      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">取消</el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="!file.name || conDisabled">确定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import * as utils from '@/utils/utils'
import exportSheet from '@/utils/exportSheet'
import Storage from '@/utils/localStorage'
const storePresaleGoods = {
  // 获取门店列表接口
  search: '/plateform/a/store/condition/search',
  // 预售商品列表
  searchTableData: '/plateform/a/product/store/presale/search',
  // excel更新预售价
  excelUpdateStorePrice: '/plateform/a/product/store/presale/excelUpdateStorePrice'

}
export default {
  data () {
    return {
      conDisabled: false, // 防止多次点击确定按钮
      isDisabled: false, // 下载模板按钮防止多次点击
      title: '批量调价',
      isShow: false,
      formLabelWidth: '70px',
      file: {name: ''}, // 导入数据
      // 导入商品
      exportGoods: [],
      tempConfig: {
        platformName: '平台(禁止修改)',
        storeNo: '门店编码(禁止修改)',
        storeName: '门店名称(禁止修改)',
        presaleId: '门店商品ID(禁止修改)',
        goodsNo: 'SKU编码(禁止修改)',
        productNo: '母商品ERP编码(禁止修改)',
        productName: '商品名称(禁止修改)',
        spec: '商品规格(禁止修改)',
        price: '门店原价(禁止修改)',
        newPrice: '门店新售价（必填）'
      },

      // 预售商品集合
      presaleList: [],
      // 销售类型，1:正常，2：预售，3：长周期预售
      saleType: '',
      platformId: '',

      // 门店编号
      storeNo: '',
      // 门店信息
      storeOptions: [],

      regionCode: '' // 区域id

    }
  },
  methods: {
    /**
     * [setDefaultMesg 设置弹框默认属性]
     * @param {[type]} _params [自定义参数]
     */
    setDefaultMesg (_params) {
      this.isShow = true
      if (_params.title) {
        this.title = _params.title
      }
      if (_params.saleType) {
        this.saleType = _params.saleType
      }
      if (_params.storeNo) {
        this.storeNo = _params.storeNo
      }
      if (_params.platformId) {
        this.platformId = _params.platformId
      }

      if (_params.regionCode) {
        this.regionCode = _params.regionCode
      }

      // 清除当前选择的文件信息
      this.file = {name: ''}
      let excelFileDom = document.getElementById('excelFile')
      if(excelFileDom) {
        excelFileDom.value = ""
      }

      // 获取门店
      this.getStoreList()
    },
    /**
     * [getStoreList 获取门店列表]
     * @return {[type]} [description]
     */
    getStoreList() {
      let params = {
        regionCode: this.regionCode
      }
      params.DIFFCITY = this.regionCode ? true : false
      this.$http.post(storePresaleGoods.search, {...params}).then(res => {
        if(res.code === 0) {
          if(res.data && res.data.length) {
            let optionsArr = []
            for(let item of res.data) {
              let optionItem = {
                value: item.storeNo,
                label: item.storeNo +'-'+ item.storeName
              }
              optionsArr.push(optionItem)
            }

            this.storeOptions = optionsArr
          }else {
            this.storeOptions = []
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(获取门店列表):", e.message)
      })
    },
    /**
     * [downloadModel 下载模板]
     * @return {[type]} [description]
     */
    downloadModel () {
      if(!this.storeNo) {
        this.$message("请选择门店")
        return
      }
      this.isDisabled = true
      this.$http.post(storePresaleGoods.searchTableData, {platformId: this.platformId,saleType: this.saleType, storeNo: this.storeNo, limitFlag: false, DIFFCITY: true}).then(res => {
        setTimeout(() => {
          this.isDisabled = false
        }, 800)
        if(res.code === 0) {
          if(res.data && res.data.pageVo && res.data.pageVo.result && res.data.pageVo.result.length) {
            let tableDataArr = res.data.pageVo.result
            exportSheet.exportFileByConfig(tableDataArr, '批量调价模板.xlsx', this.tempConfig)
            this.$notify.success({
              title: '温馨提示',
              message: '批量调价模板下载成功'
            })
          }else {
            this.$notify.warning({
              title: '温馨提示',
              message: '未获取到数据，下载失败'
            })
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.isDisabled = true
        this.$notify.error({
          title: '提示(下载模板)：',
          message: e.message
        })
      })

    },
    /**
     * [handleFileSelect 选择文件发生变化]
     * @param  {[type]} e [description]
     * @return {[type]}   [description]
     */
    handleFileSelect (e) {
      let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
      // 点击取消不做任何变化
      if (!e.target.files.length) {
        return
      }
      this.file = e.target.files[0] || {name: ''}
      let idx = this.file.name.lastIndexOf(".")
      let strLength = this.file.name.length
      let postf = this.file.name.substring(idx, strLength) // 后缀名

      if (!this.file.name || postf != '.xlsx') {
        this.$notify.warning({
          title: '提示',
          message: '请选择excel文件'
        })
        // 清除当前选择的文件信息
        this.file = {name: ''}
        document.getElementById('excelFile').value = ""
        return
      }

      let items = []
      let parseConfig = {}
      for (let key in this.tempConfig) {
        parseConfig[this.tempConfig[key]] = key
      }
      items = exportSheet.parseToJSON(this.file, parseConfig)
      items.then((result) => {
        // 过滤空行
        let resultVal = result.filter(item => !!item.presaleId)
        let presaleListArr = []
        for (let item of resultVal) {
          if (!item.newPrice) {
            this.$notify.warning({
              title: '提示',
              message: `请填写(${item.productName})的门店新售价`
            })
            // 清除当前选择的文件信息
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }else {
            if(!salePriceReg.test(item.newPrice)) {
              this.$notify.warning({
                title: '提示',
                message: `(${item.productName})的门店新售价为大于0，小数保留小数点后2位`
              })
              // 清除当前选择的文件信息
              this.file = {name: ''}
              document.getElementById('excelFile').value = ""
              return
            }
          }

          let presaleListItem = {
            storePlatformGoodsId: item.presaleId,
            price: item.newPrice
          }
          presaleListArr.push(presaleListItem)
        }
        
        this.presaleList = presaleListArr
      }).catch()
    },
    /**
     * [doConfirmHandel 点击确定]
     * @return {[type]} [description]
     */
    doConfirmHandel() {
      let params = {
        regionCode: this.regionCode
      }
      params.DIFFCITY = this.regionCode ? true : false
      this.conDisabled = true
      this.$http.post(storePresaleGoods.excelUpdateStorePrice, {adjustPriceList: this.presaleList, ...params}).then(res => {
        setTimeout(() => {
          this.conDisabled = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("批量调价成功")
          this.isShow = false
          this.$emit("doSearchHandel")
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.conDisabled = false
        this.$notify.error({
          title: '提示',
          message: e.message,
          duration: 0
        })
      })
    } 
    
  }
}
</script>



